@mixin section{
padding: 60px 0;
}


// Brakepoints
@mixin mobile {
    @media screen and (min-width: 480px){
        @content
    }
};
@mixin tablet {
    @media screen and (min-width: 768px){
        @content
    }
};
@mixin desktop {
    @media screen and (min-width: 1280px) {
        @content
    }
};

@mixin retina-bgi {
@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    @content;
}
}

